<template>
    <div>
        <TopButtons/>
        <div style="height: 10px"></div>
        <filter-component :fields="filterFields" @handleSearch="handleSearch"/>
        <div style="height: 10px"></div>
        <a-alert
                message="功能提示"
                description="显示登录用户参加的会议，包括领导身份、普通参会人员身份。查看并参加会议。"
                type="info"
                showIcon
        />
        <div style="height: 10px"></div>
        <table-component
                ref="table"
                @handleOperate="handleOperate"
                :columns="columns"
                :request="tableRequest">
        </table-component>
        <meeting-check :id="meetingId" v-model="visible"/>
    </div>
</template>

<script>
    import TopButtons from '../会议列表/components/TopButtons'
    import {getAttendUserMeetingList, deleteMeeting} from '@/api/meeting'
    import tableComponent from '@/components/table'
    import filterComponent from '@/components/filter/Index'
    import MeetingCheck from '@/components/meeting/check-modal/Index'

    export default {
        components: {
            TopButtons, filterComponent, tableComponent, MeetingCheck
        },
        data() {
            return {
                modalTitle: null,
                modalVisible: false,
                meetingId: 0,
                visible: false,
                columns: [
                    {
                        title: '#',
                        dataIndex: 'index',
                        scopedSlots: {customRender: 'index'},
                    },
                    {
                        title: '会议议题',
                        dataIndex: 'title',
                        key: 'title',
                    },
                    {
                        title: '会议地点',
                        dataIndex: 'location',
                        key: 'location',
                    },
                    {
                        title: '会议开始时间',
                        dataIndex: 'opened_at',
                        key: 'opened_at',
                    },
                    {
                        title: '申请人',
                        dataIndex: 'initiate_user.name',
                        key: 'initiate_user.name',
                    },
                    {
                        title: '会议类型',
                        dataIndex: 'type_format',
                        key: 'type_format',
                    },
                    {
                        title: '参会人员',
                        dataIndex: 'attend_users_count',
                        key: 'attend_users_count',
                    },
                    {
                        title: '审核状态',
                        dataIndex: 'audit.status_format',
                        key: 'audit.status_format',
                    },
                    {
                        title: '操作',
                        dataIndex: 'operation',
                        scopedSlots: {customRender: 'operation'},
                        buttons: {
                            check: {
                                visible: true, disable:false
                            },
                            edit: {
                                visible: true, disable: row => {
                                    switch (row.type) {
                                        case 1://线下
                                            return true;
                                        case 2://线上
                                            return false;
                                        default:
                                            return true;
                                    }
                                }, title: '参加'
                            },
                        }
                    }
                ],
                tableRequest: getAttendUserMeetingList,
                filterFields:[
                    {label:'参会人员',field:'user_id'},
                    {label:'党支部',field:'department_id'},
                    {label:'开始时间',field:'start_timestamp'},
                    {label:'结束时间',field:'end_timestamp'},
                    {label:'关键字',field:'keyword'},
                ],
            }
        },
        methods: {
            handleSearch(filter) {
                this.$refs.table.handleSearch(filter)
            },
            handleCheck(id) {
                this.meetingId = id;
                this.visible = true;
            },
            handleAttend(id) {
                this.$router.push({name: '会议进行', params: {id: id}})
            },
            handleOperate(type, row) {
                switch (type) {
                    case 'check':
                        this.handleCheck(row.id);
                        break;
                    case 'edit':
                        this.handleAttend(row.id);
                        break;
                }
            },
        },
    }
</script>
